.footer {
    max-width: 282px;
    margin: 0 auto 36px;
}


@media screen and (min-width: 768px) {
    .footer {
        max-width: 740px;
        margin: 0 0 60px 60px;
    }
}

@media screen and (min-width: 1280px) {
    .footer {
        max-width: 880px;
        margin-left: 0;
    }
}